<template>
    <div class="settings-container style-settings">
        <div class="wtw-container">
            <div class="back-to-main-menu" @click.click="goToMainMenu">
                <span class="stay-black">Back</span>
            </div>
            <ul>
                <li
                    @click.stop="handleItemClick(item)"
                    v-for="item in settingItems"
                >
                    <span class="text"> {{ item.menuTitle }} </span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import Mixins from "../../../mixins/mixins";

export default {
    mixins: [Mixins],
    computed: {
        ...mapGetters({
            settingItems: "account/styleSettings",
        }),
    },
    methods: {
        goToMainMenu() {
            this.$store.commit({
                type: "menu/setCurrentMenuItem",
                item: {
                    menuContent: "main-menu",
                    component: "main-menu/main-menu.vue",
                    isMenuExpanded: false,
                },
            });
        },
        handleItemClick(item) {
            this.$store.commit({
                type: "menu/setCurrentMenuItem",
                item: {
                    menuContent: "style-setting",
                    component: "style-settings/style-setting.vue",
                    token: item.token,
                    isMenuExpanded: true,
                },
            });
        },
    },
};
</script>
